import { Toast, Notification, Modal } from 'mk-component'
import { fetch } from 'mk-utils'
import './mock.js' //mock

var _options = {}

//fetch
fetch.config({
	mock: true, //mock

	//fetchbefore,afterrestful api
	after: (response, url) => {
		if (response.result) {
			if(response.token){ //accessToken,
				fetch.config({token:response.token})
			}
			return response.value
		}
		else {
			Toast.error(response.error.message)
			throw { url, response }
		}
	}
})

function config(options) {
	Object.assign(_options, options)

	//key'^<key>$'app
		
	_options.apps && _options.apps.config({
		//'*': { webapi } //webapiweb
		'mk-app-root': {
			startAppName: 'mk-app-login'
		},
		'mk-app-login': {
			goAfterLogin: {
				appName: 'mk-app-portal'
			}
		},
		'mk-app-portal': {
			menu: [/*{
				key: '1',
				name: 'about',
				appName: 'mk-app-portal-about',
				isDefault: true
			},*/ {
				key: '1',
				name: '',
				appName: 'dialogue',
				isDefault: true
			}, {
				key: '2',
				name: '',
				appName: 'toAccess',
				isDefault: false
			}, {
				key: '3',
				name: '',
				appName: 'leaveMessage',
				isDefault: false
			}, {
				key: '4',
				name: '',
				appName: 'sessionHistory',
				isDefault: false
			}, {
				key: '5',
				name: '',
				appName: 'search',
				isDefault: false
			}, {
				key: '6',
				name: '',
				appName: 'exportManager',
				isDefault: false
			}, {
				key: '7',
				name: '',
				appName: 'setting',
				isExpand: false,
				children: [{
					key: '701',
					name: '',
					appName: 'csim'
				}, {
					key: '702',
					name: '',
					appName: 'personalExpression'
				}]
			}, {
				key: '8',
				name: '',
				appName: 'messageCenter',
				isDefault: false
			}, {
				key: '9',
				name: '',
				appName: 'statisticalData',
				isDefault: false
			}/*, {
				key: '2',
				name: 'apps',
				isExpand: true,
				children: [{
					key: '201',
					name: '',
					appName: 'mk-app-bar-graph'
				}]
			}*/]
		}
	})

	_options.targetDomId = 'app' //react renderdom
	_options.startAppName = 'mk-app-root' //app

	_options.toast = Toast //mk-meta-engine
	_options.notification = Notification //
	_options.modal = Modal //
	return _options
}

config.current = _options

export default config


