/**
 * mock.js ajax
 * mockData,
 */

import { fetch } from 'mk-utils'

const mockData = fetch.mockData

function initMockData() {
    if (!mockData.users) {
        mockData.users = [{
            id: 1,
            mobile: 13334445556,
            password: '1',
        }]
    }
}

fetch.mock('/v1/user/login', (option) => {
    initMockData()

    const user = mockData.users.find(o => o.mobile == option.mobile && o.password == option.password)

    if (user) {
        return {
            result: true,
            //token
            token: `${user.id},${user.mobile},${user.password},${user.nickname?user.nickname:''},${user.sex?user.sex:''},${user.birthday?user.birthday:''}`, 
            value: option
        }
    }
    else {
        return { result: false, error: { message: 'user:13334445556,pwd:1' } }
    }
})



